คคล” หมายถึง ข้อมูลเกี่ยวกับบุคคลซึ่งทำให้สามารถระบุตัวบุคคลนั้นได้ ไม่ว่าทางตรงหรือทางอ้อม แต่ไม่รวมถึงข้อมูลของผู้ถึงแก่กรรมโดยเฉพาะ "),_c('br'),_vm._v("     “ข้อมูลที่มีความอ่อนไหว” หมายถึง ข้อมูลส่วนบุคคลเกี่ยวกับเชื้อชาติ เผ่าพันธุ์ ความคิดเห็นทางการเมือง ความเชื่อในลัทธิ ศาสนาหรือปรัชญา พฤติกรรมทางเพศ ประวัติอาชญากรรม ข้อมูลสุขภาพ ความพิการ ข้อมูลสหภาพแรงงาน ข้อมูลพันธุกรรม ข้อมูลชีวภาพ เช่น ข้อมูลจำลองใบหน้า ข้อมูลจำลองม่านตา หรือข้อมูลจำลองลายนิ้วมือ "),_c('br'),_c('br'),_c('b',[_vm._v("ข้อมูลส่วนบุคคลที่บริษัทฯเก็บรวบรวม")]),_c('br'),_vm._v("     ในการเก็บรวบรวม และเก็บรักษาข้อมูลส่วนบุคคล บริษัทฯ จะใช้วิธีการที่ชอบด้วยกฎหมายและจำกัดเพียงเท่าที่จำเป็นตามวัตถุประสงค์การดำเนินงานของบริษัทฯ อันประกอบด้วย "),_c('br'),_c('br'),_vm._v("1.ข้อมูลส่วนตัว เช่น ชื่อ-นามสกุล, อายุ, วันเกิด, เพศ, เลขประจำตัวประชาชน, เลขที่หนังสือเดินทาง, หมายเลขที่สามารถระบุตัวตนได้ "),_c('br'),_vm._v("2.ข้อมูลสำหรับติดต่อ เช่น ที่อยู่, หมายเลขโทรศัพท์, อีเมล "),_c('br'),_vm._v("3.ข้อมูลการเข้ารับบริการ เช่น ข้อมูลการนัดหมายแพทย์ ข้อมูลส่วนบุคคลของญาติ และบริการเสริมอื่นๆ "),_c('br'),_vm._v("4.ข้อมูลการเข้าร่วมกิจกรรมทางการตลาด เช่น ข้อมูลการลงทะเบียนเพื่อร่วมกิจกรรม "),_c('br'),_vm._v("5.ข้อมูลสถิติ เช่น จำนวนผู้ป่วย ผู้ดูแล ผู้ให้บริการ และการเข้าชมเว็บไซต์ "),_c('br'),_vm._v("6.ข้อมูลด้านสุขภาพ รายงานที่เกี่ยวกับสุขภาพกาย และสุขภาพจิต การดูแลสุขภาพของท่าน ผลการทดสอบจากห้องทดลอง และการวินิจฉัย "),_c('br'),_vm._v("7.ข้อมูลที่เกี่ยวข้องกับการใช้ยาและการแพ้ยาของท่าน "),_c('br'),_vm._v("8.ข้อมูล Feedback และผลการรักษาที่ท่านได้ให้ไว้ "),_c('br'),_vm._v("9.ข้อมูลที่ได้จากระบบอัตโนมัติหรืออุปกรณ์ต่าง ๆ เช่น หมายเลข IP Address, Cookie, พฤติกรรมการใช้บริการ, ประวัติการใช้บริการ, เสียง, ภาพถ่าย, ภายเคลื่อนไหว, ชื่อบัญชี Social Media, Chat, Geolocation "),_c('br'),_c('br'),_vm._v("โดยบริษัทฯ จะดำเนินการเก็บรวบรวมข้อมูลส่วนบุคคลเมื่อได้รับความยินยอมจากเจ้าของข้อมูลส่วนบุคคลก่อน ยกเว้นในกรณีดังต่อไปนี้ "),_c('br'),_c('br'),_vm._v("1.เพื่อปฏิบัติตามสัญญา กรณีการเก็บรวบรวม ใช้ หรือเปิดเผยข้อมูลส่วน