<script>
export default {
    name: "register-select-type",
    data() {
        return {
            windowHeight: window.innerHeight,
            current_scroll: '',
            scroll: '',
            scroll_height: '',
            pdpa_accept: false,
            padding_bottom: 200,
        };
    },
    // beforeCreate() {
    //     // this.$store.commit('initialiseStore');
    //     this.$store.commit('initialiseStore');
    // },
    mounted() {
        // //console.log(this.$refs.myButton);
        // //console.log(document.getElementById('pdpa_box_id').scrollHeight)
        // //console.log(document.getElementById('pdpa_box_id').scrollTop)
        //console.log(this.$refs.pdpa_box.scrollHeight)
        // this.scroll_height = this.$refs.pdpa_box.scrollHeight

        this.scroll = this.$refs.pdpa_box
        this.scroll_height = this.scroll.scrollHeight
        this.scroll.addEventListener("scroll", event => {
            this.current_scroll = this.scroll.scrollTop
        }, {
            passive: true
        });

        // var scrollDemo = document.getElementById('pdpa_box').scrollTop
        // scrollDemo.addEventListener("scroll", event => {
        //     output.innerHTML = `scrollTop: ${scrollDemo.scrollTop} <br>
        //                         scrollLeft: ${scrollDemo.scrollLeft} `;
        // }, { passive: true });
        // this.current_scroll = document.getElementById('pdpa_box').scrollTop
        // //console.log(this.$ref.textbox)
        //console.log(localStorage.getItem('store'))
        //console.log(this.$store.state)
        this.pdpa_update()

        
        window.addEventListener('resize', this.onResize);
        
    },
    beforeDestroy() {
        window.removeEventListener('resize', this.onResize);
    },
    watch: {
        current_scroll(val) {
            var cr_scroll = Math.floor((this.scroll_height-val-this.windowHeight))
            var pd_bt = Math.floor((-1*this.padding_bottom))

            
            // //console.log('cr_scroll->',cr_scroll)
            // //console.log('pd_bt->',pd_bt)
            if(cr_scroll <= pd_bt){
                //console.log('77777')
                this.pdpa_accept = true
            }
            // if ((this.scroll_height-val-this.windowHeigh) <= (-1*this.padding_bottom)+10) {
            //     this.pdpa_accept = true
            //     // this.checkPdpa()
            //     //console.log('at bt')
            // }

        }
    },

    methods: {
        onResize() {
           this.windowHeight = window.innerHeight
        }, 
        pdpa_update() {
            // this.pdpa_accept = 
            // //console.log(this.$store.state.register_form.pdpa)
            // this.pdpa_accept = this.$store.state.register_form.pdpa
        },
        checkPdpa() {
            if (this.pdpa_accept) {
                const user_type = this.$store.state.register_form.user_type
                const lineid = this.$store.state.register_form.lineid
                this.$store.commit('setRegisterForm', {
                    pdpa_accept: this.pdpa_accept,
                    user_type: user_type,
                    lineid: lineid,
                    highpressure: 0,
                    hyperlipidemia: 0,
                    alzheimer: 0,
                    heart: 0,
                    diabetes: 0,
                    arrhythmai: 0,
                    depress: 0,
                });
                this.$router.push('/register_form')
            }
        },
        goBack() {
            this.$router.push({
                path: '/register_select_type',
                replace: true
            })
        }
    },
};
</script>

